import React from 'react'
import Footer from '../component/Footer';
import Header from '../component/Header';
import Portfolio from '../component/Portfolio';
const PagePortfolio = () => {
  return (
    <>
    <Header/>
    <div className='pageprot py-5'>
       <div className='py-3'>
          <Portfolio/>
       </div>
    </div>
    <Footer/>
  </>
  )
}

export default PagePortfolio;
