import React from 'react'
import OurservicesCard from './OurservicesCard';
import OursercData from './OurservcardData';
import Button from '@mui/material/Button';
import {useNavigate} from "react-router-dom";
const Ourservices = () => {
  
 const navigate = useNavigate();
  const goToServices = () =>{
    navigate("/services")
  }


  return (
   <>
      <aside className="services py-5">
        <div className="container">
            <div className="row">
                <div className="col-md-12 text-center">
                    <h3>Our <span className='subtitle'>Services</span></h3>
                </div>
            </div>
            <div className="row pt-5">
               {
                OursercData.map((value)=>{
                  return(
                   
                      <OurservicesCard 
                        key={value.id}
                        icons={value.icons} 
                        title={value.title} 
                        disc={value.disc} />
                     
                  )
                })
               }

              <div className="col-md-12 text-center">
                  <Button variant="contained" className='rmbtn' onClick={()=> goToServices()}>Read More</Button>
              </div>

            </div>
        </div>
      </aside>
   </>
  )
}

export default Ourservices;
