import React from 'react'
import PillsHomecard from './PillsHomecard';
import pillalldata from './PillsHomecardData';
import Pillsdecorative from './Pillsdecorative';
import PillsdecorativeData from './PillsdecorativeData';
import Pillsfacades from './Pillsfacades';
import PillsfacadesData from './PillsfacadesData';
import Pillspreferd from './Pillspreferd';
import pillspreferdData from './pillspreferdData';
import Pillsrealings from './Pillsrealings';
import PillsrealingsData from './PillsrealingsData';

const Portfolio = () => {
  return (
    <>
        <section className="portfolio py-5">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 text-center">
                        <h2>Our Work Portfolio</h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <ul className="nav nav-pills my-4 d-flex justify-content-center" id="pills-tab" role="tablist">
                            <li className="nav-item" role="presentation">
                            <button className="nav-link active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab" aria-controls="pills-home" aria-selected="true">All</button>
                            </li>
                            <li className="nav-item" role="presentation">
                            <button className="nav-link" id="pills-decorative-tab" data-bs-toggle="pill" data-bs-target="#pills-decorative" type="button" role="tab" aria-controls="pills-decorative" aria-selected="false">decorative</button>
                            </li>
                            <li className="nav-item" role="presentation">
                            <button className="nav-link" id="pills-facades-tab" data-bs-toggle="pill" data-bs-target="#pills-facades" type="button" role="tab" aria-controls="pills-facades" aria-selected="false">facades</button>
                            </li>
                            <li className="nav-item" role="presentation">
                            <button className="nav-link" id="pills-preforated-tab" data-bs-toggle="pill" data-bs-target="#pills-preforated" type="button" role="tab" aria-controls="pills-preforated" aria-selected="false">preforated</button>
                            </li>
                            <li className="nav-item" role="presentation">
                            <button className="nav-link" id="pills-realings-tab" data-bs-toggle="pill" data-bs-target="#pills-realings" type="button" role="tab" aria-controls="pills-realings" aria-selected="false">realings</button>
                            </li>
                        </ul>
                        </div>
                     
                           <div className="tab-content" id="pills-tabContent">
                            <div className="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab" tabIndex="0">
                            <div className="card_wrap">
                            {
                                pillalldata.map((cval)=>{
                                    return(
                                       
                                          <PillsHomecard key={cval.id} imgscr={cval.imgscr}/>
                                        
                                    )
                                })
                            }
                            </div>
                            </div>
                            <div className="tab-pane fade" id="pills-decorative" role="tabpanel" aria-labelledby="pills-decorative-tab" tabIndex="0">
                            <div className="card_wrap">

                                {
                                    PillsdecorativeData.map((dval)=>{
                                        return(
                                            
                                               <Pillsdecorative key={dval.id} imgscr={dval.imgscr}/>
                                            
                                        )
                                    })
                                }

                               
                              </div>
                            </div>
                        <div className="tab-pane fade" id="pills-facades" role="tabpanel" aria-labelledby="pills-facades-tab" tabIndex="0">
                            <div className="card_wrap">
                                {
                                    PillsfacadesData.map((tval)=>{
                                        return(
                                            
                                              <Pillsfacades key={tval.id} imgscr={tval.imgscr}/>
                                           
                                        )
                                    })
                                }
                                
                              </div>
                            </div>

                            <div className="tab-pane fade" id="pills-preforated" role="tabpanel" aria-labelledby="pills-preforated-tab" tabIndex="0">
                            <div className="card_wrap">
                                
                                {
                                    pillspreferdData.map((fval)=>{
                                        return(
                                            
                                              <Pillspreferd key={fval.id} imgscr={fval.imgscr}/>
                                           
                                        )
                                    })
                                }

                              

                              </div>
                            </div>

                            <div className="tab-pane fade" id="pills-realings" role="tabpanel" aria-labelledby="pills-realings-tab" tabIndex="0">
                            <div className="card_wrap">
                                
                                {
                                    PillsrealingsData.map((gval)=>{
                                        return(
                                            
                                              <Pillsrealings key={gval.id} imgscr={gval.imgscr}/>
                                           
                                        )
                                    })
                                }

                                

                              </div>
                            </div>

                         </div>
                       
                </div>
            </div>
        </section>
    </>
  )
}

export default Portfolio;
