import React from 'react'


const OurservicesCard = (props) => {
  return (
   <>
      <div className="col-md-4">
         <div className="card text-center">
            <div className="card-body">
            <span className='icons'>{props.icons}</span>
            <h5>{props.title}</h5>
            <p>{props.disc}</p>
            </div>
         </div>
      </div>
   </>
  )
}

export default OurservicesCard;
