import React from 'react'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

const Getintouch = () => {

    let googleMap = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d121058.92836615497!2d73.79292730116279!3d18.524766325796897!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2bf2e67461101%3A0x828d43bf9d9ee343!2sPune%2C%20Maharashtra!5e0!3m2!1sen!2sin!4v1655539435066!5m2!1sen!2sin";


    let mapStyle = {
        width : "100%", 
        height : "100%", 
        border : "0", 
        allowfullscreen : "", 
        loading : "lazy", 
        referrerpolicy : "no-referrer-when-downgrade",
    }

  return (
     <section className="getintouch py-5">
        <div className="container">
            <div className="row">
                <div className="col-md-12 text-center">
                   <h4>Get in <span className='subtitle'>touch</span></h4>
                </div>
            </div>
            <div className="row pt-5 g-0">
                <div className="col-md-6">
                   <div className="forms">
                      <form action="">
                        <div className="row">
                            <div className="col-md-12">
                            <TextField className='mb-4 border-white'  fullWidth id="outlined-basic" label="Your Name" variant="outlined" />
                            </div>
                            <div className="col-md-6">
                            <TextField className='mb-4 border-white' fullWidth id="outlined-basic" label="Phone Number" variant="outlined" />
                            </div>
                            <div className="col-md-6">
                            <TextField className='mb-4 border-white' fullWidth id="outlined-basic" label="Services" variant="outlined" />
                            </div>
                            <div className="col-md-12">
                            <TextField className='mb-4 border-white'  fullWidth id="outlined-basic" label="Message" variant="outlined" />
                            <Button className='rmbtn' variant="contained">Read More</Button>
                            </div>
                        </div>
                      </form>
                   </div>
                </div>
                <div className="col-md-6">
                    <div className="map">
                    <iframe src={googleMap} style={mapStyle}></iframe>
                    </div>
                </div>
            </div>
        </div>
     </section>
  )
}

export default Getintouch;
