import React from 'react'
import logo from '../images/logo.png'; 
import { FaMapMarkerAlt } from "react-icons/fa";
import { FaPhoneAlt } from "react-icons/fa";
import { FaEnvelope } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";
import { FaTwitter } from "react-icons/fa";
import { FaLinkedinIn } from "react-icons/fa";
import { FaYoutube } from "react-icons/fa";
import Navbar from './Navbar';




const Header = () => {


  return (
    <>
       <header className="header py-3">
        <div className="container">
            <div className="row">
                <div className="col-12 col-md-3">
                    <img src={logo} alt="" className='logo' />
                </div>
                <div className="col-12 col-md-2">
                   <p className='d-flex align-items-center'><span><FaMapMarkerAlt className='hicons'/></span>Pune</p>
                </div>
                <div className="col-12 col-md-2">
                    <p className='d-flex align-items-center'><span><FaPhoneAlt className='hicons'/></span>(91) - 9112391531</p>
                </div>
                <div className="col-12 col-md-3 bgw">
                    <p className='d-flex align-items-center'><span><FaEnvelope className='hicons'/></span><a href='mailto:vinod.patil377@gmail.com'>vinod.patil377@gmail.com</a></p>
                </div>
                <div className="col-12 col-md-2 d-flex justify-content-end">
                   <ul>
                    <li><FaFacebookF className='socIcons'/></li>
                    <li><FaTwitter className='socIcons'/></li>
                    <li><FaLinkedinIn className='socIcons'/></li>
                    <li><FaYoutube className='socIcons'/></li>
                   </ul>   
                </div>
            </div>
        </div>
       </header>
       <Navbar/>
   
    </>
  )
}

export default Header;
