import React from 'react'
import TestMcard from './TestMcard';
import TestMcardData from './TestMcardData';

const Testimonial = () => {
  return (
    <>
        <aside className="testimonial py-5">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 text-center">
                        <h5>Testimonial</h5>
                    </div>
                </div>
                <div className="row py-5">
                 <div id="carouselExampleIndicators" className="carousel slide" data-bs-ride="true">
                    <div className="carousel-indicators">
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                    </div>
                    <div className="carousel-inner">
                      
                        {
                            TestMcardData.map((elem,i)=>{
                                return(
                                      <div key={i} className={i === 0 ? "carousel-item active":"carousel-item"} data-bs-interval="10000">
                                          <TestMcard key={elem.id} imgscr={elem.imgscr} content={elem.content} name={elem.name}/>
                                       </div>
                                   
                                )
                            })
                        }
         
                    </div>
                    </div>
                </div>
            </div>
        </aside>
    </>
  )
}

export default Testimonial;
