import React from 'react'

const TestMcard = (props) => {
  return (
   <>
    <div className="row">
        <div className="col-md-10 d-flex m-auto align-items-center respoMobile">
        <img src={props.imgscr} className="d-block" alt="slider Img"/>
        <div className='figcap'>
          <p>{props.content}</p>
          <h6 className='mt-2'><strong>{props.name}</strong></h6>
        </div>
        </div>
    </div>
   </>
  )
}

export default TestMcard;
