import React from 'react'
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import {useNavigate} from "react-router-dom";


const SliderCard = (props) => {

const navigate = useNavigate();

const goToAboutus = () =>{
  navigate("/about")
}
const goToContact = () =>{
  navigate("/contact")
}

  return (
        <>
            
               <div className='pepper'>
               <img src={props.imgurl} className="d-block w-100" alt="..."/>
                <div className="carousel-caption d-none d-md-block">
                    <h2>{props.content}</h2>
                    <Stack spacing={2} direction="row">
                        <Button variant="contained" onClick={()=> goToAboutus()}>{props.readmtlink}</Button>
                        <Button variant="outlined" onClick={()=> goToContact()}>{props.contactlink}</Button>
                    </Stack>
                </div>
               </div>
           
       </>
  )
}

export default SliderCard;
