import React from 'react'
import Button from '@mui/material/Button';
import welImg from "../images/welcome.png";
import {useNavigate} from "react-router-dom";

const Welcome = () => {
 
   const navigate = useNavigate(); 

 const goToAboutus = () =>{
    navigate("/about")
 }   

  return (
     <>
        <section className="welcome py-5">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-md-6">
                        <h2>Welcome to <span className="subtitle">Troweld</span> </h2>
                        <p>Simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has s</p>
                        <Button className='rmbtn' variant="contained" onClick={()=> goToAboutus()}>Read More</Button>
                    </div>
                    <div className="col-md-6 text-center">
                        <img src={welImg} alt="welcome" className='img-fluid'/>
                    </div>
                </div>
            </div>
        </section>
     </>
  )
}

export default Welcome;
